@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topnav {
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
  background-color: #e4e4e4;
  overflow: hidden;
  z-index: 1;
  font-family: 'Oxanium';
  position: fixed;
  top: 0;
  width: 100%;
}
.logo {
  width: 18px;
  height: 18px;
}

.topnav a {
  float: right;
  display: block;

  color: #0f2235;
  font-size: 120%;
  text-align: center;
  padding: 8px 16px;
  text-decoration: none;
}

@media screen and (max-width: 600px) {
  .links a {
    display: none;
  }
}

.links a:hover {
  background-color: #a9a9a9;
}

.subhead {
    font-size: 300%;
    color: #0f2235;
    font-family: 'Oxanium';
}

  .ProjectCard {  
    width: 33vw;
    display: table;
    margin: 2em;
    color: #0f2235;
    height: 40%;
    border-radius: 25px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
    text-align: center;
    background: linear-gradient(to left,#e4c0c6,white);

  }
  
.Row {
  display: table-row;
}
  .ProjectCardImg img {
    float: left;
    
    width: 226px;
    height: 290px;
    margin-top: 5px;
    border-radius: 10%;
    padding: 2em;
    object-fit: cover;
  }
  
  
  .ProjectCardImg {
   
    overflow: visible;
    margin: auto;
    padding: 1em;
   
  }
  .ProjectCardInfo {
    padding: 2em;
    text-align: center;
    
  }
  
  
  .ProjectCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
  .ProjectCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
  .ProjectCardBottom::-webkit-scrollbar-thumb {
    background-color: #bec4c8; 
    border-radius:3px;
  }
 


 
.UserCard {
  flex: 1 1;
  position: relative;
  padding-bottom: 1em;
  width: 290px;
  display: table;
  color: black;
  height: 360px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px black;
  margin: 10px;
  background: #0c2b5e;
  background: linear-gradient(to left, #cdcdcd, white);
}
.OuterWrap {
  display: flex;
  justify-content: center;
}

.UserCard img {
  width: 200px;
  height: 200px;
  margin: 20px 0 0 0;
  border-radius: 50%;
  border: 3px solid #0f2235;
  object-fit: cover;
}

.UserCardTop {
  height: 67%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.UserCardBottom {
  min-height: 40%;
  overflow: auto;
  min-width: 100%;
  max-width: 100%;
  padding: 0 10px 5px;
  overflow-wrap: break-word;
}

.UserCardBottom::-webkit-scrollbar {
  width: 3px;
  height: 1px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  z-index: 10;
}
.UserCardBottom::-webkit-scrollbar-track {
  background-color: #0c2b5e;
}
.UserCardBottom::-webkit-scrollbar-thumb {
  background-color: #bec4c8;
  border-radius: 3px;
}

.Home {
  text-align: center;
}

.TeamMemberList {
  position: absolute;
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.ProjectCardList {
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

@media screen and (max-width: 600px) {
  .ProjectCardList {
    display: block;
    margin: auto;
  }
  .TeamMemberList {
    display: block;
    margin: auto;
  }
}

@media (max-width: 900px) {
  .TeamMemberList {
    grid-template-columns: repeat(2, 1fr);
  }
}

.wrapper {
  display: flex;
  justify-content: center;
}

.wrapperPC {
  display: flex;
  justify-content: center;
}

.owl {
  display: flex;
  align-items: center;
}

.sls {
  display: flex;
  align-items: center;
}

.PrivacyPolicy {
  color: #0f2235;
  height: 40%;
  border-radius: 25px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
  background: linear-gradient(to left, #e4c0c6, white);
  margin: 2em;
  padding: 1em 2em;
}

* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  margin: 0;
}
body {
  font-size: 92%;
  font-family: 'Oxanium', sans-serif;
  color: #0f2235;
  background-image: url(/static/media/1.ec243b7e.jpg);
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
h2,
h3,
h4,
h5 {
  margin: 0 0 3px;
}

